.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

main {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

ul {
  max-width: 20rem;
  width: 100%;
  list-style-type: none;
  text-align: left;
  margin: 0;
  padding-top: 0;
}

.filterApplied {
  opacity: 1.0
}

.filterInactive {
  opacity: 0.5;
}

.App-header h1 {
  margin-bottom: 1rem;
}

.App-header h1 button {
  vertical-align: middle;
  font-size: 1.05rem;
}

.listControls button {
  font-size: 1.05rem;
}